@import "../../../assets/styles/variable";

#footer {
  margin: 2rem auto 3rem auto;
  max-width: $max-width;
  width: $width;

  .line {
    height: 1px;
    background: #DDD2E4;
    margin-bottom: 2.2rem;
  }

  .content {
    display: flex;
    align-items: start;
    justify-content: space-between;

    .links {
      gap: 5rem;
      align-items: start;

      .column {
        gap: 1rem;

        span {
          font-size: 1rem;
          font-weight: bold;
          margin-bottom: 1rem;
        }

        a {
          font-size: 1rem;
        }
      }
    }
  }

  .subtitle {
    font-size: 0.8rem;
    margin-bottom: 1.5rem;
    max-width: 60%;
  }
}

@media (max-width: 900px) {
  #footer {
    .content {
      flex-direction: column;
      .links .column span{
        margin-bottom: 0;
        margin-top: 1rem;
      }
    }

    .footer_socials {
      display: none;
    }
  }
}
